<script>
import { getCustomerDataAll, searchCustomerData } from "api/customers";
import PageHeader from "components/common/menu/PageHeader";
import CustomerForm from "views/master/CustomerForm";

export default {
  name: "CustomerList",
  components: {
    PageHeader,
    CustomerForm,
  },
  data() {
    return {
      ruleForm: {
        customer_name: "",
        personnel_name: "",
        telephone: "",
        is_delete: false,
      },
      customer_name: "",
      personnel_name: "",
      telephone: "",
      tableData: [],
      loading: false,
      dialogFormVisible: false,
    };
  },
  created() {
    this.getCustomerDataAll();
  },
  methods: {
    getCustomerDataAll() {
      this.loading = true;
      getCustomerDataAll()
        .then((res) => {
          if (res.data.status === 1) {
            this.tableData = res.data.data;
            this.loading = false;
          } else {
            this.loading = false;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    searchCustomerData() {
      let values = {};
      values["customer_name"] = this.ruleForm.customer_name;
      values["personnel_name"] = this.ruleForm.personnel_name;
      values["telephone"] = this.ruleForm.telephone;
      values["is_delete"] = this.ruleForm.is_delete;
      searchCustomerData(values)
        .then((res) => {
          this.tableData = res.data.data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.searchCustomerData();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    closeDialog() {
      this.dialogFormVisible = false;
    },
    reloadList() {
      this.dialogFormVisible = false;
      this.getCustomerDataAll();
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.getCustomerDataAll();
    },
    handleEdit(index, row) {
      this.customer_code = row.customer_code;
      this.dialogFormVisible = true;
    },
    createBtn() {
      this.customer_code = "";
      this.dialogFormVisible = true;
    },
    handleDelete(index, row) {
      console.log(index, row);
    },
  },
};
</script>

